@font-face {
  font-family: cascadia;
  src: url('/src/components/fonts/Cascadia.ttf') format('truetype');
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: cascadia;
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  width: .5rem;
}

::-webkit-scrollbar-thumb{
  background: var(--cta-color);
}

body{
  background-color: #f1f1f1;
}

:root{
  --cta-color: #0030aa;
  --gradient-cta-color: #15f;
  --details-color: #aaa;
}

#body-in-grid{
  width: 100%;
  height: 100%;
  position: absolute;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
}

.divisor{
  display: block;
  text-align: center;
  margin-top: 3rem;
}

.externalUrl{
  color: var(--cta-color);
  text-decoration: none;
}

#burguer-menu,  #mobile-filter{
  display: none;
}

/* NAVBAR */

.navBar{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.presentation{
  border: 2px solid var(--cta-color);
  margin-top: 1rem;
  width: 13rem;
  height: 4.7rem;
  position: fixed;
}

.name-container{
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, var(--cta-color), var(--gradient-cta-color));
  position: absolute;
  width: max-content;
  height: max-content;
  padding: .8rem 1.5rem;
  margin-left: .2rem;
  margin-top: -.4rem;
}

.navigation{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 7rem;
  position: fixed;
}

.navigation li{
  list-style-type: none;
  font-size: 1.5rem;
  color: var(--details-color);
  margin-top: 1rem;
  transition: .3s;
}

.navigation li:hover{
  color: #15f;
  cursor: pointer;
}

/* ABOUT ME */

.about-me{
  margin-top: 15%;
}

.photo-and-desc{
  display: flex;
  padding: 0 10%;
  align-items: center;
}

.my-photo{
  width: 10rem;
  height: 10rem;
  object-fit: cover;
  outline: 3px solid var(--cta-color);
}

.description{
  margin-left: 2rem;
  text-align: justify;
}

/* KNOWLEDGE CONTAINER */

.knowledge-container{
  padding: 0 10%;
  margin-top: 3rem;
}

.knowledge{
  display: flex;
  justify-content: space-between;
}

.knowledgeSubItem{
  display: flex;
  justify-content: space-between;
  padding-left: 5%
}

/* WORK CONTAINER */

.works-container{
  padding: 0 10%;
  margin-top: 3rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.border-work{
  width: 20rem;
  height: 13.5rem;
  margin: 1.5rem 2%;
  background-color: #aaa;
  transition: .3s;
}

.border-work:hover{
  background-color: var(--cta-color);
  cursor: pointer;
}

.work{
  width: 20rem;
  height: 13.5rem;
  background-color: #f1f1f1;
  border: 1px solid var(--details-color);
  position: absolute;
  margin-left: .5rem;
  margin-top: -.5rem;
  transition: .1s;
}

.work:hover{
  border: 1px solid var(--gradient-cta-color);
}

.work-title{
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  filter: opacity(0);
  transition: .3s;
  color: #111;
}

.work:hover .work-title{
  filter: opacity(1);
  top: -15%;
}

.work img{
  width: 100%;
  height: 100%;
  object-fit: fill;
}

/* CONTACT CONTAINER */

.contact-container{
  margin-top: 3rem;
  margin-bottom: 10%;
}

.contact{
  display: flex;
  padding: 0 10%;
  justify-content: space-between;
}

.border-button-contact{
  border: 2px solid var(--cta-color);
  margin: .5rem 5%;
  transition: .1s;
  height: 1.7rem;
  width: 7rem;
}

.border-button-contact:hover{
  background-color: #aaa;
  border-color: #aaa;
}

.button-contact{
  background-image: linear-gradient(to right, var(--cta-color), var(--gradient-cta-color));
  padding: .2rem .5rem;
  transition: .1s;
  text-decoration: none;
  color: #f1f1f1;
  border: 1px solid var(--gradient-cta-color);
  position: absolute;
  margin-top: -.4rem;
  margin-left: .1rem;
}

.button-contact:hover{
  background-image: none;
  background-color: #f1f1f1;
  color: #111;
  cursor: pointer;
}

/* SOCIAL MEDIA */

.social-media-container{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
  margin-right: 1rem;
  margin-bottom: 1rem;
  position: fixed;
  right: 0;
  bottom: 0;
}

.social-media{
  background-image: linear-gradient(to right, var(--cta-color), var(--gradient-cta-color));
  margin-top: 1rem;
  transition: .1s;
}

.social-media:hover{
  background-image: none;
  background-color: #aaa;
}

.media-img{
  background-color: #f1f1f1;
  transition: .1s;
  border: 1px solid var(--gradient-cta-color);
  padding: .5rem;
  margin-left: .1rem;
  margin-bottom: -.1rem;
}

@media only screen and (max-width: 800px){
  
  
  /* NAVBAR */
  
  .presentation{
    margin-top: 1rem;
    margin-left: 3rem;
    width: 11rem;
    height: 4rem;
  }

  .name-container{
    padding: .4rem .5rem;
    margin-left: .1rem;
    margin-top: -.4rem;
  }

  .navigation li{
    font-size: 1.2rem;
  }

  /* ABOUT ME */

  .photo-and-desc{
    flex-direction: column;
  }

  .description{
    margin-top: 3rem;
  }

  /* KNOWLEDGE */

  .knowledge-container{
    padding: 0 15%;
  }

  /* WORK */

  .border-work, .work{
    width: 15rem;
    height: 10rem;
  }
}

@media only screen and (max-width: 680px){

  .notOverflow{
    overflow: hidden !important;
  }

  .photo-and-desc{
    margin-top: 3rem;
    padding: 0;
  }

  .social-media-container{
    display: none;
  }

  #burguer-menu{
    display: block;
    position: absolute;
    right: 1rem;
    top: 1rem;
  }

  #mobile-filter{
    background-image: linear-gradient(to right, var(--cta-color), var(--gradient-cta-color));
    width: 0;
    height: 100vh;
    z-index: 10;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 100vw;
    right: 0;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: .3s;
    overflow-x: hidden;
  }

  .moveFilter{
    width: 100vw !important;
    left: 0 !important;
  }

  #close-filter{
    position: absolute;
    right: 1rem;
    top: 3rem;
    color: var(--details-color);
    font-weight: 600;
    transition: .3s;
    padding: .1rem .4rem;
  }

  .filter-mobile-nav{
    list-style-type: none;
    color: var(--details-color);
    transition: .3s;
    width: max-content;
    margin: auto;
    padding: .2rem .5rem;
  }

  .presentation{
    margin-top: 1rem;
    margin-left: 5rem;
    width: 6rem;
    height: 2rem;
    position: absolute;
  }

  .name-container{
    padding: .2rem .4rem;
    margin-left: .1rem;
    margin-top: -.5rem;
  }

  .name-container h1{
    font-size: .8rem !important;
  }

  .name-container p{
    font-size: .6rem;
  }

  .navigation{
    display: none;
  }

  .navigation li{
    font-size: .9rem;
    margin-left: 2rem;
  }

  .border-work, .work{
    width: 12rem;
    height: 7rem;
  }

  /* ABOUT ME */

  .description{
    margin-left: 0;
  }

  /* KNOWLEDGE */

  .knowledge-container{
    padding: 0;
  }

  /* CONTACT */
  
  .contact{
    display: flex;
    padding: 0;
    justify-content: space-between;
  }

  .contact img{
    height: 1rem;
    width: 1rem;
  }

  .contact p{
    padding-left: .2rem !important;
  }

  .border-button-contact{
    margin: .5rem 1%;
    height: 1.3rem;
    width: 5rem;
  }
  
  .button-contact{
    padding: .1rem .1rem;
    margin-top: -.4rem;
    margin-left: .1rem;
    font-size: .8rem;
  }

  .media-img{
    padding: .2rem;
    height: 2rem;
    width: 2rem;
  }
}